import BaseBay from "./baseBay"
import DeckRow from "./deckRow"

export default class DeckBay extends BaseBay {
	constructor(info) {
		super(info)
	}

	createRow(no, info) {
		return new DeckRow(no, info)
	}
	createTierRow() {
		var info = []
		for (var i = 1; i <= this.tierCount; ++i)
			info.push((80 + 2 * i).toString())
		return new DeckRow("", info)
	}
}


