//船贝图上的箱位
export default class Slot {
  constructor(x = 0, y = 0) {
    this.x = x
    this.y = y
    this.cx = 0
    this.cy = 0
    this.cntrs = []
  }
  draw(ctx) {
    if(ctx.invalidRect)//指定了失效区
    {
      let xin = (this.x > ctx.invalidRect.x && this.x < ctx.invalidRect.x+ ctx.invalidRect.cx)|| (this.x+this.cx > ctx.invalidRect.x && this.x +this.cx< ctx.invalidRect.x+ ctx.invalidRect.cx)
      if(!xin) {
        let yin = (this.y > ctx.invalidRect.y && this.y < ctx.invalidRect.y + ctx.invalidRect.cy) || (this.y + this.cy > ctx.invalidRect.y && this.y + this.cy < ctx.invalidRect.y + ctx.invalidRect.cy)
        if (!yin)
          return
      }
    }
    ctx.clearRect(this.x,this.y,this.cx,this.cy)
    this.cntrs.forEach(c=>c.draw(ctx))
    ctx.beginPath()
    ctx.rect(this.x, this.y, this.cx, this.cy)
    if (this.selected) {
      ctx.strokeStyle="#0000ff"
      ctx.lineWidth =3

    }
    ctx.stroke()
    if (this.selected) {
      ctx.strokeStyle ="#000000"
      ctx.lineWidth =1
    }
  }
  move(cx, cy) {
    this.x += cx;
    this.y += cy;
    this.cntrs.forEach(c=> {
      c.x = this.x
      c.y = this.y
    })
  }
  findSlotByPoint(x, y) {
    var cx = x - this.x
    if (cx > 0 && cx < this.cx) {
      var cy = y - this.y
      if (cy > 0 && cy < this.cy)
        return this
    }
    return null
  }
  addCntr(cntr)
  {
    cntr.x = this.x
    cntr.y = this.y
    cntr.cx = this.cx
    cntr.cy = this.cy
    this.cntrs.push(cntr)
  }
  findSlotByCntr(no) {
    var bFind = false
    this.cntrs.forEach(c=> {
      if (c.no === no)
        bFind = true
    })
    if(bFind)
      return this
    return null
  }
  setSlotSize(cx,cy) {
    this.cx = cx
    this.cy = cy
     this.cntrs.forEach(c=> {
      c.cx = this.cx
      c.cy = this.cy
    })
  }
}



