export default class BaseBay {
	constructor(info,slotWidth = 12) {
		this.rows = []
		var row
		this.tierCount = 0
		for (var r in info) {
			row = this.createRow(r, info[r])
			this.addRow(row)
		}
		this.tierRow = this.createTierRow()
		this.rows.forEach(r=>r.slots.forEach((s,i)=>s.stowLoc = r.label.no + this.tierRow.slots[i].no))
	}
	addRow(row) {
		this.rows.push(row)
		if (row.slots.length > this.tierCount)
			this.tierCount = row.slots.length
	}
	move(cx, cy) {
		this.tierRow.move(cx , cy)
		this.rows.forEach(r=>r.move(cx , cy))
	}
	draw(ctx) {
		this.tierRow.draw(ctx)
		this.rows.forEach(r=>r.draw(ctx))
	}
	findSlotByLoc(pos) {
		let tierIndex = -1
		let rowNo = parseInt(pos.substr(3, 2))
		let tierNo = parseInt(pos.substr(5, 2))
		//在甲板上查找对应层号
		tierIndex = this.tierRow.slots.findIndex(t=>t.no == tierNo)
		//找到层号，再找对应的排
		if (tierIndex >= 0) {
			var row = this.rows.find(r=>r.label.no ==rowNo)
			return row.slots[tierIndex]
		}
		return null
	}
	findSlotByPoint(x, y) {
		var slot = this.tierRow.findSlotByPoint(x, y)
		if (slot)
			return slot
		for (var i = 0; i < this.rows.length; ++i) {
			slot = this.rows[i].findSlotByPoint(x, y)
			if (slot)
				return slot
		}
		return null
	}

	findSlotByCntr(no) {
		var slot
		for (var i = 0; i < this.rows.length; ++i) {
			slot = this.rows[i].findSlotByCntr(no)
			if (slot)
				return slot
		}
		return null
	}
	setSlotSize(cx,cy) {
		var i=0
		this.tierRow.setSlotSize(cx,cy)
		this.rows.forEach(r=> {
			r.setSlotSize(cx, cy)
			r.move(cx * ++i, 0)
		})
	}
}


