import Label from "./label"

export default class SideShipBay{
	constructor(i) {
		this.no = i.preFill(2)
		this.sNo = (i-1).preFill(2)
		this.bNo = (i+1).preFill(2)
	}
	draw(ctx) {
		ctx.fillStyle = "red"
		ctx.fillText(this.no, this.x+12, this.y)
		ctx.fillStyle = "blue"
		ctx.fillText(this.sNo, this.x+21, this.y + 15)
		ctx.fillText(this.bNo, this.x+3, this.y + 15)
		ctx.fillStyle = "cyan"
		let x= this.x+2
		let y = this.y +115
		ctx.rect(x,y,32,15)
		ctx.fillRect(x,y,32,15)
		y += 15
		ctx.rect(x,y,32,15)
		ctx.fillRect(x,y,32,15)
		y+=20
		ctx.rect(x,y, 32, 15)
		ctx.fillRect(x,y, 32, 15)
		y+=15
		ctx.rect(x,y, 32, 15)
		ctx.fillRect(x,y, 32, 15)
		ctx.stroke()
	}
}


