import DeckBay from "./deckBay"
import CabinBay from "./cabinBay"
import Container from "./container"
import Label from "./label"

export default class ShipBay {
	constructor(obj) {
		this.no = obj.no
		this.label = new Label(this.no)
		if (obj.deckBay)
			this.deckBay = new DeckBay(obj.deckBay)
		else
			this.deckBay = null
		if (obj.cabinBay)
			this.cabinBay = new CabinBay(obj.cabinBay)
		else
			this.cabinBay = null
		if (obj.cntr)
			obj.cntr.forEach(cntr=>this.addCntr(new Container(cntr)))
		this.rowCount = Math.max(this.deckBay?this.deckBay.rows.length:0,this.cabinBay?this.cabinBay.rows.length:0)
	}
	draw(ctx) {
		this.label.draw(ctx)
		this.cabinBay.draw(ctx)
		this.deckBay.draw(ctx)
	}
	findSlotByLoc(pos) {
		//在甲板上查找对应层号
		var slot = this.deckBay.findSlotByLoc(pos)
		if(slot)
			return slot
		//在舱下查找对应层号
		return this.cabinBay.findSlotByCntr(pos)
	}
	addCntr(cntr) {
		var stowLoc = cntr.getStowLoc()

		var slot = this.findSlotByLoc(stowLoc)
		if (slot) 
			slot.addCntr(cntr);
	}

	findSlotByPoint(x, y) {
		var slot = this.deckBay.findSlotByPoint(x, y)
		if (slot)
			return slot
		return this.cabinBay.findSlotByPoint(x, y)
	}

	findSlotByCntr(no) {
		var slot = this.deckBay.findSlotByCntr(no)
		if (slot)
			return slot
		return this.cabinBay.findSlotByCntr(no)
	}
	
	move(cx,cy){
		this.label.move(cx, cy)
		this.cabinBay.move(cx, cy)
		this.deckBay.move(cx, cy)
	}
	setSlotSize(cx,cy){
		this.label.setSlotSize(cx,cy)
        this.label.move(this.rowCount*cx /2,0)
        if(this.deckBay) {
            this.deckBay.setSlotSize(cx, cy)
            this.deckBay.move(0, cy * (this.deckBay.tierCount + 1))
        }
        if(this.cabinBay)
        {
            this.cabinBay.setSlotSize(cx, cy)
            this.cabinBay.move(0 + this.deckBay ? (this.deckBay.rows.length - this.cabinBay.rows.length) * cx/2 : 0, cy * ((this.deckBay ? this.deckBay.tierCount + 1 : 0) + 1))
        }
	}
	filtSize(cx = 300,cy = 300){
		var xCells = (this.rowCount+1)
		var slotWidth = Math.floor(cx / xCells)
		var yCells = 1+(this.deckBay?this.deckBay.tierCount +1:0) + (this.cabinBay?this.cabinBay.tierCount+1:0)
		var slotHeight = Math.floor(cy / yCells)
		slotHeight = slotWidth = Math.min(slotHeight,slotWidth)
		this.setSlotSize(slotWidth,slotHeight)
		this.move((cx-xCells*slotWidth)/2,(cy - yCells*slotHeight)/2)
	}
}


