import SideShip from './sideShip'
import SideShipBay from './sideShipBay'
var sshipMaps = new Map()
export default class ShipMap {
	static get shipMaps(){return sshipMaps}
	constructor(div,name) {
		sshipMaps.set(name,this)
		var canvas
		if (typeof div === "string")
			div = document.getElementById(div);
		if (div.tagName !== "CANVAS") {
			canvas = document.createElement("canvas")
			canvas.height = 300
			canvas.width = 900
			div.appendChild(canvas)
		}
		else
			canvas = div
		this.canvas = canvas
		this.name = name;
		var context = canvas.getContext("2d");
		var i = 0;
		try{
			i = name.substr(2) * 2 + 1
		}
		catch (e)
		{
		}
		this.ship = new SideShip()
		this.ship.draw(context)
	}
}






