export default class Clock {
  constructor(time = new Date()) {
    this.time = time
    this.x = 50
    this.y = 50
    this.radius =45
    this.inTime = true
  }
  getAngle()
  {
    return ((this.time.getHours()%12)*30 + (this.time.getMinutes()+ this.time.getSeconds()/60)/2)*Math.PI/180 - Math.PI/2
  }
  drawClock(ctx)
  {
    ctx.lineWidth = 1
    ctx.beginPath()
    ctx.arc(0,0,this.radius,0,2*Math.PI)
    ctx.stroke()
    for(let i=0;i<60;++i) {
      let len = 6
      let width = 1
      if (i % 5 === 0) {
        len = 8
        width = 2
      }
      ctx.lineWidth = width
      ctx.beginPath()
      let angle = i*Math.PI / 30
      ctx.moveTo(this.radius * Math.cos(angle), this.radius * Math.sin(angle))
      ctx.lineTo((this.radius-len) * Math.cos(angle), (this.radius-len) * Math.sin(angle))
      ctx.stroke()
    }

  }

  drawPointer(ctx)
  {
    ctx.beginPath()
    ctx.arc(0,0,4,0,2*Math.PI)
    ctx.fillStyle = "black"
    ctx.fill()
    let len = 10
    let secAgl = this.time.getSeconds()*Math.PI/30 - Math.PI/2
    ctx.beginPath()
    ctx.moveTo(0,0)
    ctx.lineTo((this.radius-len) * Math.cos(secAgl),  (this.radius-len) * Math.sin(secAgl))
    ctx.stroke()
    len += 5
    let minAgl = (this.time.getMinutes()+this.time.getSeconds()/60)*Math.PI/30 - Math.PI/2
    ctx.beginPath()
    ctx.moveTo(0,0)
    ctx.lineTo((this.radius-len) * Math.cos(minAgl), (this.radius-len) * Math.sin(minAgl))
    ctx.lineWidth = 2
    ctx.stroke()
    len += 5
    let hourAgl = this.getAngle()
    ctx.beginPath()
    ctx.moveTo(0,0)
    ctx.lineTo((this.radius-len) * Math.cos(hourAgl), (this.radius-len) * Math.sin(hourAgl))
    ctx.lineWidth = 3
    ctx.stroke()
  }

  drawTime(ctx)
  {
    ctx.beginPath()
    ctx.moveTo(0,0)
    ctx.lineTo(0, - this.radius)
    ctx.arc(0,0,this.radius,-Math.PI/2,this.getAngle())
    ctx.closePath()
    ctx.fillStyle = "red"
    ctx.fill()
    this.drawPointer(ctx)
    ctx.fillStyle = "cyan"
    ctx.fillRect(-17, - 8,34,16);
    ctx.fillStyle = "black"
    ctx.fillText(`${this.time.getHours().preFill(2,'0')}:${this.time.getMinutes().preFill(2,'0')}`,-14, 5)
  }

  draw(ctx) {
    ctx.clearRect( - this.radius, - this.radius,2*this.radius,2*this.radius)

    ctx.lineWidth = 1
    this.drawTime(ctx)
    this.drawClock(ctx)
  }

}



