export default class Ship {
	constructor(length =800,width=300,height=60) {
		this.x = 80
		this.y = 80
		this.length = length
		this.width = width
		this.height = height
		this.headLength = 60
		this.tailLength = 40
		this.houseFromHead = this.length - this.tailLength - 260;
		this.houseLength = 60;
	}
	draw(ctx) {
		ctx.beginPath()
		this.drawHead(ctx)
		this.drawBody(ctx)
		this.drawTail(ctx)
		this.drawHouse(ctx)
		ctx.stroke()

	}
	onclick(e) {
		
	}
}


