import Stomp from "./stomp"
function reconnect(mq)
{
	mq.client._cleanUp()
	var ws = new WebSocket(mq.url)
	mq.client = Stomp.over(ws)
	mq.client.connect(mq.usr, mq.passwd, ()=>{
		console.log(`reconnect to ${mq.url}`)
		for (var des in mq.subCallBack)
			mq.client.subscribe(des, mq.subCallBack[des]);
	}, ()=> console.log('error'), mq.host)
	ws.onclose =  ()=>reconnect(mq)
}
export default class RabbitMQ {
	constructor(url,usr,passwd,host){
		this.url = url
		this.usr=usr
		this.passwd = passwd
		this.host = host
		var ws = new WebSocket(url)
		this.rpcCallBack = {}
		this.subCallBack = {}
		this.client = Stomp.over(ws)
		this.client.connect(usr, passwd, ()=>console.log(`connect to ${url}`), ()=> console.log('error'), host)
		this.count = 0
		this.client.onreceive = frame=>{
			let callback = null
			if(frame.headers.rpc_id && (callback = this.rpcCallBack[frame.headers.rpc_id])){
				callback(frame);
				delete this.rpcCallBack[frame.headers.rpc_id]
			}
			else {
				console.log('unhandled message:')
				console.log(frame)
			}
		}
		ws.onclose =  ()=>reconnect(this)
	}
	subscribe(destination,callback){
		this.subCallBack[destination] = callback;
		return this.client.subscribe(destination,callback)
	}
	unsubscribe(id){
		return this.client.unsubscribe(id)
	}
	send(destination, body){
		return this.client.send(destination,null,body);
	}
	call(destination,body,callback){
		let headers = {"reply-to":"/temp-queue/rpc",rpc_id:this.count}
		this.rpcCallBack[this.count++] = callback
		return this.client.send(destination,headers,body);
	}
}


