import Clock from './clock'
var sclockMaps = new Map()
var timer
var lastMinutes = 0
export default class ClockMap {
	static get clockMaps() {
		return sclockMaps
	}

	constructor(div, name) {
		sclockMaps.set(name, this)
		var canvas
		if (typeof div === "string")
			div = document.getElementById(div);
		if (div.tagName !== "CANVAS") {
			canvas = document.createElement("canvas")
			canvas.height = 100
			canvas.width = 100
			div.appendChild(canvas)
		}
		else
			canvas = div
		this.canvas = canvas
		this.name = name;
		var context = canvas.getContext("2d")
		this.clock = new Clock()
		context.translate(this.clock.x,this.clock.y)
		this.clock.draw(context)
	}

	static start() {
		timer = setInterval(()=> {
			let time = new Date()
			/*let minutes = time.getMinutes()
			if(minutes === lastMinutes)
				return
			lastMinutes = minutes*/
			sclockMaps.forEach(c=> {
				if(!c.clock.inTime)
					return
				c.clock.time = time
				var context = c.canvas.getContext("2d")
				c.clock.draw(context)
			})
		}, 1000)
	}
	static stop()
	{
		clearInterval(timer)
	}
	setTime(time){
		this.clock.time = time
		this.clock.inTime = false
		var context = this.canvas.getContext("2d")
		this.clock.draw(context)
	}
}






