import BaseBay from "./baseBay"
import CabinRow from "./cabinRow"

export default class CabinBay extends BaseBay {
	constructor(info) {
		super(info)
	}
	createRow(no, info) {
		return new CabinRow(no, info)
	}
	createTierRow() {
		var info = []
		for (var i = this.tierCount; i >= 1; --i)
			info.push((2 * i).preFill(2))
		return new CabinRow("", info)
	}
}


