import Slot from "./slot"
export default class Label extends Slot {
	constructor(no) {
		super()
		this.no = no
	}
	get drawRect(){return this._drawRect}
	set drawRect(bdrawRect){ this._drawRect = bdrawRect}
	draw(ctx) {
		if(this._drawRect)
			super.draw(ctx)
		let font = ctx.font;
		ctx.font=`${this.cy*0.8}px sans-serif`
		ctx.fillText(this.no, this.x, this.y + this.cy * 0.8)
		ctx.font = font
	}
}


