import ShipBay from './shipBay'
var sbayMaps = new Map()
export default class BayMap {
	static get bayMaps(){return sbayMaps}
	constructor(div,name) {
		sbayMaps.set(name,this)
		var canvas
		if (typeof div === "string")
			div = document.getElementById(div);
		if (div.tagName !== "CANVAS") {
			canvas = document.createElement("canvas")
			canvas.height = 300
			canvas.width = 300
			div.appendChild(canvas)
		}
		else
			canvas = div
		this.canvas = canvas
		canvas.onclick=e=>{//给canvas添加点击事件
			e=e||event;//获取事件对象
			//获取事件在canvas中发生的位置
			var x=e.offsetX;
			var y=e.offsetY;
			var slot = this.shipBay.findSlotByPoint(x,y)
			if(slot && slot != this.selectedSlot) {
				if(this.selectedSlot){
					this.selectedSlot.selected = false
					let {x,y,cx,cy} = this.selectedSlot
					x-=3;
					y-=3;
					cx+= 6
					cy +=6
					context.invalidRect={x,y,cx,cy}
					context.clearRect(x,y,cx,cy)
					this.shipBay.draw(context)
					delete  context.invalidRect

				}
				slot.selected = true
				slot.draw(context)
				this.selectedSlot = slot
			}
		}
		this.name = name;
		var context = canvas.getContext("2d");
		var i = 0;
		try{
			i = name.substr(2) * 2 + 1
		}
		catch (e)
		{
		}
		this.shipBay = new ShipBay({
			no: i.preFill(3),
			deckBay: {
				"r15": 9,
				"r13": 9,
				"r11": 9,
				"r09": 9,
				"r07": 9,
				"r05": 9,
				"r03": 9,
				"r01": 9,
				"r02": 9,
				"r04": 9,
				"r06": 9,
				"r08": 9,
				"r10": 9,
				"r12": 9,
				"r14": 9,
				"r16": 9
			},
			cabinBay: {
				"r13": 5,
				"r11": 5,
				"r09": 6,
				"r07": 7,
				"r05": 8,
				"r03": 8,
				"r01": 9,
				"r00": 9,
				"r02": 9,
				"r04": 8,
				"r06": 8,
				"r08": 7,
				"r10": 6,
				"r12": 5,
				"r14": 4,
			},
			cntr: [{no: "000", stowLoc: "0010384", cntrClass: "I"},
				{no: "001", stowLoc: "0010182", cntrClass: "I"},
				{no: "002", stowLoc: "0010282", cntrClass: "E", commStatus: 5},
				{no: "003", stowLoc: "0010382", cntrClass: "I", commStatus: 4},
				{no: "004", stowLoc: "0010482", cntrClass: "I", commStatus: 4},
				{no: "005", stowLoc: "0010582", cntrClass: "F", commStatus: 4},
				{no: "006", stowLoc: "0010682", cntrClass: "I", commStatus: 4},
				{no: "007", stowLoc: "0010782", cntrClass: "E", commStatus: 4},
				{no: "008", stowLoc: "0010882", cntrClass: "E", commStatus: 4},
				{no: "009", stowLoc: "0010484", cntrClass: "E", commStatus: 4}]
		})
		this.shipBay.filtSize(300,279);
		this.shipBay.draw(context)
	}
}


