//船贝图上的一排
import Slot  from "./slot"
import Label from "./label"

export default class ShipRow {
	constructor(no, info) {
		no = no.substr(1)
		this.label = new Label(no)
		this.slots = []
		var slot
		if (info instanceof Array) {
			for (var i = 0; i < info.length; ++i) {
				slot = new Label(info[i])
				this.addSlot(slot)
			}
		} else if (!isNaN(info)) {
			for (var i = 0; i < info; ++i) {
				slot = new Slot()
				this.addSlot(slot)
			}
		} else
			throw "非法参数"
	}
	addSlot(slot) {
		this.slots.push(slot)
	}

	move(cx, cy) {
		this.label.move(cx, cy)
		this.slots.forEach(s=>s.move(cx, cy))
	}
	draw(ctx) {
		this.label.draw(ctx)
		this.slots.forEach(s=>s.draw(ctx))
	}
	findSlotByPoint(x, y) {
		var slot = this.label.findSlotByPoint(x, y)
		if (slot)
			return slot
		for (var i = 0; i < this.slots.length; ++i) {
			slot = this.slots[i].findSlotByPoint(x, y)
			if (slot)
				return slot
		}
		return null
	}
	findSlotByCntr(no) {
		var slot
		for (var i = 0; i < this.slots.length; ++i) {
			slot = this.slots[i].findSlotByCntr(no)
			if (slot)
				return slot
		}
		return null
	}
	setSlotSize(cx,cy) {
		this.label.setSlotSize(cx,cy)
		this.slots.forEach(s=>s.setSlotSize(cx,cy))
		this.initPosition(cy);
	}
}



