import Ship from "./ship"
import SideShipBay from "./sideShipBay"

export default class SideShip extends Ship {
	constructor(length =800,width=300,height=60) {
		super(length,width,height)
		this.y = 60
		this.sideBays = []
		for(let i=0;i<18;++i)
			this.addBay(new SideShipBay(i*4+2))
	}
	drawHead(ctx)
	{
		var x = this.x + this.length-this.headLength;
		var y =  this.y + this.height
		ctx.moveTo(x, y)
		x+= this.headLength /2
		ctx.lineTo(x,y)
		for(var i=0;i<360;i+=20){
			var step = i*this.height/720;
			ctx.lineTo(x+ this.headLength/5*Math.sin(i*Math.PI /180),y-step)
		}
		x= this.x+ this.length
		y = this.y -5
		ctx.lineTo(x,y)
		x-= this.headLength*0.4
		ctx.lineTo(x, y)
		y+= 5;
		ctx.lineTo(x,y)
		x-= this.headLength*0.6
		ctx.lineTo(x, y)
		x= this.x+this.length - this.headLength*0.15
		y= this.y - 5
		ctx.moveTo(x,y)
		y-= this.height/8
		ctx.lineTo(x,y);
		x-= this.headLength*0.05
		ctx.lineTo(x,y);
		y-= this.height/8
		ctx.lineTo(x,y);
		x-= this.headLength*0.05
		ctx.lineTo(x,y)
		y+= this.height/8
		ctx.lineTo(x,y);
		x-= this.headLength*0.05
		ctx.lineTo(x,y);
		y+= this.height/8
		ctx.lineTo(x,y);
		ctx.stroke()
		x+=this.headLength*0.075
		y-= this.height/4
		ctx.moveTo(x,y)
		y-= this.height/8
		ctx.lineTo(x,y)
		x += this.headLength*0.05
		y += this.height/16
		ctx.moveTo(x,y)
		x -= this.headLength*0.1
		ctx.lineTo(x,y)
	}
	drawTail(ctx)
	{
		ctx.moveTo(this.x, this.y)
		ctx.lineTo(this.x+ this.tailLength,this.y)
		ctx.moveTo(this.x, this.y)
		var x= this.x;
		var y = this.y+this.height/2
		ctx.lineTo(x,y)
		x+=this.tailLength/2
		ctx.lineTo(x,y)
		for(var i=0;i<360;i+=20){
			var step = i*this.height/720;
			ctx.lineTo(x+ this.tailLength/5*Math.sin(i*Math.PI /180),y+step)
		}
		ctx.lineTo(this.x+ this.tailLength, this.y+ this.height)
	}
	drawBody(ctx)
	{
		var x = this.x + this.tailLength;
		var y =  this.y
		var bodyLen = this.length -  this.headLength - this.tailLength;
		ctx.moveTo(x,y)
		x+=bodyLen
		ctx.lineTo(x,y)
		y+= this.height
		ctx.moveTo(x,y)
		x -= bodyLen
		ctx.lineTo(x,y)
	}
	drawHouse(ctx)
	{
		var x = this.x + this.length - this.houseFromHead;
		var y =  this.y
		ctx.moveTo(x,y)
		y-=60
		ctx.lineTo(x,y)
		x-= this.houseLength/2
		ctx.lineTo(x,y)
		y+= 20
		ctx.lineTo(x,y)
		x-= this.houseLength/2
		ctx.lineTo(x,y)
		y+= 40
		ctx.lineTo(x,y)
	}
	draw(ctx)
	{
		super.draw(ctx)
		this.sideBays.forEach(b=>b.draw(ctx))
	}
	addBay(bay)
	{
		let bayWidth = 36;
		let bayOffset = 0;
		bay.x =  this.sideBays.length > 0 ? this.sideBays[this.sideBays.length-1].x - bayWidth - bayOffset:this.x + this.length - this.headLength
		//bay.label.x = this.x + this.length - this.headLength - this.sideBays.length * 20
		if(bay.x < (this.x+ this.length-this.houseFromHead) && bay.x > (this.x+ this.length-this.houseFromHead - this.headLength)) {
			bay.x = this.x + this.length - this.houseFromHead - this.houseLength - bayWidth - bayOffset
		}
		bay.y = this.y - 50;
		bay.cx = bayWidth
		bay.cy = 20
		this.sideBays.push(bay)
	}
}


