		/*ctx.beginPath()
		ctx.fillStyle = "red"
		if(this.cntrClass === "E" )
		{
			ctx.moveTo(this.x,this.y)
			ctx.lineTo(this.x+this.cx,this.y)
			ctx.lineTo(this.x+ this.cy/2,this.y+this.cy)
			ctx.closePath()
		}
		else if(this.cntrClass === "I" )
		{
			ctx.moveTo(this.x,this.y+this.cy)
			ctx.lineTo(this.x+this.cx,this.y+this.cy)
			ctx.lineTo(this.x+ this.cy/2,this.y)
			ctx.closePath()
		}
		else
		{}
		ctx.fill()*/
		ctx.fillStyle = (this.cntrClass === "E" ? "rgb(255,255,0)" : this.cntrClass === "I" ? "rgb(255,0,0)" : "rgb(128,128,128)")
		ctx.fillRect(this.x, this.y, this.cx, this.cy)
		//������б����ʾָ��״̬
