Number.prototype.preFill = function( length, prefix='0' ) {
	var s = this.toString()
	var p = []
	for( var i = s.length;i <= length;++i )
		p.push( "" )
	return p.join( prefix ) + s
}

import Stomp from "./stomp"
import Container from "./container"
import BayMap1 from "./bayMap"
import ShipMap1 from "./shipMap"
import ClockMap1 from "./clockMap"
import RMQ from "./rabbitMQ"
export  var BayMap = BayMap1
export  var ShipMap = ShipMap1
export  var ClockMap = ClockMap1

export function RabbitMQ(url,usr,passwd,host) {

	function updateQC(msg) {
		var baymap = BayMap.bayMaps.get(msg.qc)
		if (baymap) {
			var c = new Container(msg.cntr)
			baymap.shipBay.addCntr(c)
			var context = baymap.canvas.getContext("2d")
			c.draw(context)
		}
	}
	let mq = new RMQ(url,usr,passwd,host)
	setTimeout(function(){ mq.subscribe('/exchange/ex_brodcast/shipmonitor.#', function (d) {
		try {
			var msg = JSON.parse(d.body)
			if (msg) {
				updateQC(msg)
			}
			console.log(msg)
		}
		catch (e) {
		}
	})},1000)
	return mq
}

export function RabbitRpc(url,usr,passwd,host) {
	let mq = new RMQ(url,usr,passwd,host)
	return mq
}


